<template>
  <v-card class="mx-auto carddata h-100" @click="single(idcard)">
    <v-img cover height="250" :src="image"></v-img>
    <div>
      <v-card-item class="px-sm-3 px-1">
        <div
          class="d-flex flex-sm-row flex-column flex-no-wrap align-start justify-sm-space-between justify-start"
        >
          <h3>
            {{ title.split('.')[0] }}
            {{ $t("SR") }}
          </h3>
          <span class="span-type" v-if="type">{{ type }}</span>
          <span v-else></span>
        </div>
        <span class="me-1 subtitle font-weight-medium" v-if="subtitle">{{
          subtitle
        }}</span>
      </v-card-item>
      <div
        class="d-flex flex-column flex-no-wrap align-start justify-sm-space-between justify-start flex-no-wrap"
      >
        <v-card-title class="pa-0">
          <v-icon icon="mdi-map-marker-outline"></v-icon>
          {{ city }}
        </v-card-title>

        <span class="px-4 subtitle">
          {{ date }}
        </span>
      </div>
      <div class="d-flex align-center justify-start flex-wrap">
        <div
          v-for="(item, i) in numwidth"
          :key="i"
          class="px-2 mb-2"
          style="width: calc(100% / 2)"
        >
          <div v-if="item.show_outside == 1" class="d-flex align-center">
            <img :src="item.image" style="width: 25px" :alt="item.name" />
            <div class="px-1" v-for="(ele, i) in item.values" :key="i">
              <div v-if="typeof ele === 'object'">
                <p>
                  {{
                    ele[0] === "true"
                      ? "نعم"
                      : ele[0] === "false"
                      ? "لا"
                      : ele[0] === "0"
                      ? ele[0]
                      : ele[0]
                  }}
                </p>
              </div>
              <div v-else>
                <p>
                  {{ ele }}
                </p>
              </div>

              <!-- {{ typeof ele === "object" ? ele[0] : ele }} 
               {{ ele[0]=='true'?"نعم":"لا" }} -->
            </div>
          </div>
        </div>
      </div>
      <v-card-actions class="mt-2">
        <div
          class="text-center w-100"
          style="position: absolute; right: 0; bottom: 8px"
        >
          <button class="previewads" elevation="0" @click="single(idcard)">
            {{ $t("Showdetail") }}
            <v-icon class="arrow" icon="mdi-chevron-left"> </v-icon>
          </button>
        </div>
      </v-card-actions>
    </div>
  </v-card>
</template>

<script>
export default {
  props: {
    image: {
      type: String,
      required: true,
    },
    title: {
      type: String,
    },
    subtitle: {
      type: String,
    },
    city: {
      type: String,
    },
    date: {
      type: String,
    },
    type: {
      type: String,
    },

    numwidth: {
      type: Array,
    },
    idcard: {
      type: Number,
    },
    idsupport: {
      type: Number,
    },
  },
  methods: {
    single(id) {
      this.$router.push({ name: "SingleAd", params: { id } });
    },
  },
};
</script>

<style scoped>
.card {
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: hidden;
  width: 300px;
}

.card-image {
  width: 100%;
  height: auto;
}

.card-content {
  padding: 16px;
}
.carddata .subtitle {
  /* color: #969696; */
  font-size: 20px;
}
.span-type {
  background: #fede2c;
  border-radius: 8px;
  padding: 4px 16px;
}
.carddata .previewads {
  width: 90%;
  height: 45px;
}
</style>
